.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: var(--z-confetti);
    overflow: hidden;
}

.particle {
    position: absolute;
    top: 0;
    pointer-events: none;
}

/* Celebration Overlay */
.celebrationOverlay {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(8px);
    z-index: calc(var(--z-confetti) - 1);
}

.celebrationCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-4);
    padding: var(--space-10) var(--space-12);
    background: linear-gradient(135deg,
            rgba(139, 92, 246, 0.2) 0%,
            rgba(236, 72, 153, 0.1) 100%);
    border: 1px solid rgba(139, 92, 246, 0.3);
    border-radius: var(--radius-2xl);
    text-align: center;
    box-shadow:
        0 0 80px rgba(139, 92, 246, 0.4),
        var(--shadow-lg);
}

.celebrationEmoji {
    font-size: 4rem;
    line-height: 1;
}

.celebrationTitle {
    font-size: 2rem;
    font-weight: 800;
    font-family: var(--font-display);
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin: 0;
}

.celebrationMessage {
    font-size: 1.125rem;
    color: var(--text-secondary);
    margin: 0;
}